import React, {useState, useEffect} from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Modal, ModalBody, ModalFooter, ModalHeader} from 'reactstrap';
import axios from 'axios';

function App() {
  const baseUrl="http://localhost/apiPrueba/index.php";
  const [data, setData]=useState([]);
  const [modalInsertar, setModalInsertar]= useState(false);
  const [modalEditar, setModalEditar]= useState(false);
  const [modalEliminar, setModalEliminar]= useState(false);
  const [clienteSeleccionado, setClienteSeleccionado]=useState({
    id: '',
    nombre: '',
    apellido: '',
    cedula: '',
    telefono: '',
    email: ''
  });

  const handleChange=e=>{
    const {name, value}=e.target;
    setClienteSeleccionado((prevState)=>({
      ...prevState,
      [name]: value
    }))
    console.log(clienteSeleccionado);
  }

  const abrirCerrarModalInsertar=()=>{
    setModalInsertar(!modalInsertar);
  }

  const abrirCerrarModalEditar=()=>{
    setModalEditar(!modalEditar);
  }

  const abrirCerrarModalEliminar=()=>{
    setModalEliminar(!modalEliminar);
  }

  const peticionGet=async()=>{
    await axios.get(baseUrl)
    .then(response=>{
      console.log(response.data);
      setData(response.data);
    }).catch(error=>{
      console.log(error);
    })
  }

  const peticionPost=async()=>{
    var f = new FormData();
    f.append("nombre", clienteSeleccionado.nombre);
    f.append("apellido", clienteSeleccionado.apellido);
    f.append("cedula", clienteSeleccionado.cedula);
    f.append("telefono", clienteSeleccionado.telefono);
    f.append("email", clienteSeleccionado.email);
    f.append("METHOD", "POST");
    await axios.post(baseUrl, f)
    .then(response=>{
      setData(data.concat(response.data));
      abrirCerrarModalInsertar();
    }).catch(error=>{
      console.log(error);
    })
  }

  const peticionPut=async()=>{
    var f = new FormData();
    f.append("nombre", clienteSeleccionado.nombre);
    f.append("apellido", clienteSeleccionado.apellido);
    f.append("cedula", clienteSeleccionado.cedula);
    f.append("telefono", clienteSeleccionado.telefono);
    f.append("email", clienteSeleccionado.email);
    f.append("METHOD", "PUT");
    await axios.post(baseUrl, f, {params: {id: clienteSeleccionado.id}})
    .then(response=>{
      var dataNueva= data;
      dataNueva.map(framework=>{
        if(framework.id===clienteSeleccionado.id){
          framework.nombre=clienteSeleccionado.nombre;
          framework.apellido=clienteSeleccionado.apellido;
          framework.cedula=clienteSeleccionado.cedula;
          framework.telefono=clienteSeleccionado.telefono;
          framework.email=clienteSeleccionado.email;
        }
      });
      setData(dataNueva);
      abrirCerrarModalEditar();
    }).catch(error=>{
      console.log(error);
    })
  }

  const peticionDelete=async()=>{
    var f = new FormData();
    f.append("METHOD", "DELETE");
    await axios.post(baseUrl, f, {params: {id: clienteSeleccionado.id}})
    .then(response=>{
      setData(data.filter(framework=>framework.id!==clienteSeleccionado.id));
      abrirCerrarModalEliminar();
    }).catch(error=>{
      console.log(error);
    })
  }

  const seleccionarFramework=(framework, caso)=>{
    setClienteSeleccionado(framework);

    (caso==="Editar")?
    abrirCerrarModalEditar():
    abrirCerrarModalEliminar()
  }

  useEffect(()=>{
    peticionGet();
  },[])

  return (
    <div style={{textAlign: 'center', backgroundColor:'#BFBCD9'}}>
<br />
  <h1>Lista de Clientes</h1>
      <button className="btn btn-success" onClick={()=>abrirCerrarModalInsertar()}>Agregar Cliente</button>
      <br /><br />
    <table className="table table-dark table-striped">
      <thead>
        <tr>
          <th>ID</th>
          <th>Nombre</th>
          <th>apellido</th>
          <th>Documento de Identidad</th>
          <th>Telefono</th>
          <th>Email</th>
          <th>Opciones</th>
        </tr>
      </thead>
      <tbody>
        {data.map(framework=>(
          <tr key={framework.id}>
            <td>{framework.id}</td>
            <td>{framework.nombre}</td>
            <td>{framework.apellido}</td>
            <td>{framework.cedula}</td>
            <td>{framework.telefono}</td>
            <td>{framework.email}</td>
          <td>
          <button className="btn btn-primary" onClick={()=>seleccionarFramework(framework, "Editar")}>Editar</button> {"  "}
          <button className="btn btn-danger" onClick={()=>seleccionarFramework(framework, "Eliminar")}>Eliminar</button>
          </td>
          </tr>
        ))}


      </tbody> 

    </table>


    <Modal isOpen={modalInsertar}>
      <ModalHeader>Añadir Cliente</ModalHeader>
      <ModalBody>
        <div className="form-group">
          <label>Nombre: </label>
          <br />
          <input type="text" className="form-control" name="nombre" onChange={handleChange}/>
          <br />
          <label>Apellido: </label>
          <br />
          <input type="text" className="form-control" name="apellido" onChange={handleChange}/>
          <br />
          <label>Documento de Identidad: </label>
          <br />
          <input type="number" className="form-control" name="cedula" onChange={handleChange}/>
          <br />
          <label>Telefono:</label>
          <br />
          <input type="number" className="form-control" name="telefono" onChange={handleChange}/>
          <br />
          <label>Email: </label>
          <br />
          <input type="text" className="form-control" name="email" onChange={handleChange}/>
          <br />
        </div>
      </ModalBody>
      <ModalFooter>
        <button className="btn btn-primary" onClick={()=>peticionPost()}>Insertar</button>{"   "}
        <button className="btn btn-danger" onClick={()=>abrirCerrarModalInsertar()}>Cancelar</button>
      </ModalFooter>
    </Modal>


    
    <Modal isOpen={modalEditar}>
      <ModalHeader>Editar Cliente</ModalHeader>
      <ModalBody>
        <div className="form-group">
          <label>Nombre: </label>
          <br />
          <input type="text" className="form-control" name="nombre" onChange={handleChange} value={clienteSeleccionado && clienteSeleccionado.nombre}/>
          <br />
          <label>Apellido: </label>
          <br />
          <input type="text" className="form-control" name="apellido" onChange={handleChange} value={clienteSeleccionado && clienteSeleccionado.apellido}/>
          <br />
          <label>Documento de Identidad: </label>
          <br />
          <input type="number" className="form-control" name="cedula" onChange={handleChange} value={clienteSeleccionado && clienteSeleccionado.cedula}/>
          <br />
          <label>Telefono: </label>
          <br />
          <input type="number" className="form-control" name="telefono" onChange={handleChange} value={clienteSeleccionado && clienteSeleccionado.telefono}/>
          <br />
          <label>Email: </label>
          <br />
          <input type="text" className="form-control" name="email" onChange={handleChange} value={clienteSeleccionado && clienteSeleccionado.email}/>
          <br />
        </div>
      </ModalBody>
      <ModalFooter>
        <button className="btn btn-primary" onClick={()=>peticionPut()}>Editar</button>{"   "}
        <button className="btn btn-danger" onClick={()=>abrirCerrarModalEditar()}>Cancelar</button>
      </ModalFooter>
    </Modal>

    <Modal isOpen={modalEliminar}>
        <ModalBody>
        ¿Estás seguro que deseas eliminar el cliente {clienteSeleccionado && clienteSeleccionado.nombre}?
        </ModalBody>
        <ModalFooter>
          <button className="btn btn-danger" onClick={()=>peticionDelete()}>
            Sí
          </button>
          <button
            className="btn btn-secondary"
            onClick={()=>abrirCerrarModalEliminar()}
          >
            No
          </button>
        </ModalFooter>
      </Modal>

    </div>
  );
}

export default App;
